body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.warning {
  color: pink;
}
li.none
{
    list-style-type: none;
}
.a_text {
  color: blue;
}
.a_text:hover { 
  background-color: yellow;
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
}
.popup_inner {
  position: absolute;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background: white;
}


